class AllAroundDude extends Character {
    constructor(game, startPosX, startPosY) {
        super(game, startPosX, startPosY);
        var opts = {
            id: "AllAroundDude",
            name: "All Around Dude",
            maxSpeed: 400,
            acceleration: 2,
            deceleration:1,
            currentDir: 1,
            hurtboxes: [
                new Hurtbox(startPosX, startPosY, 15, 35)
            ],
            hitboxes: {
                basicAttack: [
                    new Hitbox({ name: "first", xOffset: 15, yOffset: 20, width: 5, height: 5, damage: 4, angle: 35, knockback: 2, growth: 0, hitstun: 60, startFrame: 8, endFrame: 14,  cooldown: 14}),
                    new Hitbox({ name: "extended", xOffset: 20, yOffset: 25, width: 10, height: 10, damage: 5, angle: 35, knockback: 2, growth: 0, hitstun: 25, startFrame: 14, endFrame: 21,  cooldown: 7})
                ]
            },
            turnDelay: 0.15,
            weight: 1,
            airSpeed: 300,
            jumpPower: 4,
            jumpHeight: 10,
            allowedJumps: 2,
            jumpThreshold: {
                up: 5,
                down: 15
            }
        };

        super.initialise(opts);
    }

    drawActions(stage) {
        super.drawActions(stage);
    }
}